* {
  box-sizing: border-box;
  transition: all .3s;
}

.page {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  max-width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  padding: 50px 0;
  display: grid;
}

.main__card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 32px 16px 16px;
  display: flex;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card-photo {
  width: 160px;
  height: 134px;
}

.card-name {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card-product-code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  line-height: 14px;
}

.card-reviews {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card-reviews-count {
  color: #060b35;
  line-height: 14px;
}

.card-price {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
}

.card-price-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card-price-label {
  color: #616070;
  font-size: 12px;
}

.card-button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card-button:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
}

.main__card:hover {
  transform: scale(1.2);
}

.main__card:hover .card-name {
  color: #34568b;
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

